import { createApp } from "../main";

const renderVueComponentToString = require("vue-server-renderer/basic.js");

global.renderServer = context => {
  console.log("renderServer in entry server");
  console.log(`
===context start===`);
  console.log(context);
  console.log(`===context end===
  `);
  // å› ä¸ºæœ‰å¯èƒ½ä¼šæ˜¯å¼‚æ­¥è·¯ç”±é’©å­å‡½æ•°æˆ–ç»„ä»¶ï¼Œæ‰€ä»¥æˆ‘ä»¬å°†è¿”å›ä¸€ä¸ª Promiseï¼Œ
  // ä»¥ä¾¿æœåŠ¡å™¨èƒ½å¤Ÿç­‰å¾…æ‰€æœ‰çš„å†…å®¹åœ¨æ¸²æŸ“å‰å°±å·²ç»å‡†å¤‡å°±ç»ªã€‚
  return new Promise((resolve, reject) => {
    // è§£æ„èµ‹å€¼
    const { vm, router } = createApp();
    // è®¾ç½®æœåŠ¡å™¨ç«¯routerçš„ä½ç½®
    // ç»™è·¯ç”±æ¨ä¸€æ¡è®°å½•ï¼Œä¸Šé¢çš„{app,router}åªæ˜¯ä¸€ä¸ªå¯¹è±¡ï¼Œæ²¡æœ‰èµ°çœŸæ­£æ¸²æŸ“é‚£æ­¥ï¼Œ
    // æ‰€ä»¥åªæœ‰ä¸»åŠ¨è°ƒç”¨router.push()å®ƒæ‰ä¼šæ‰§è¡Œè¿™éƒ¨åˆ†çš„ä»£ç ï¼Œ
    // ç»™æˆ‘ä»¬åŒ¹é…åˆ°æˆ‘ä»¬è¦è°ƒç”¨çš„è¿™äº›ç»„ä»¶
    router.push(context.url);
    console.log(`context.url:${context.url}`);

    // ç­‰åˆ°routerå°†å¯èƒ½çš„å¼‚æ­¥ç»„ä»¶å’Œé’©å­å‡½æ•°è§£æå®Œ
    //router.onReadyåŸºæœ¬ä¸Šåªæœ‰åœ¨æœåŠ¡ç«¯æ‰ä¼šè¢«ç”¨åˆ°ï¼Œ
    // åœ¨è·¯ç”±è®°å½•è¢«æ¨è¿›å»çš„æ—¶å€™ï¼Œè·¯ç”±æ‰€æœ‰çš„å¼‚æ­¥æ“ä½œéƒ½åšå®Œçš„æ—¶å€™æ‰ä¼šè°ƒç”¨è¿™ä¸ªå›è°ƒï¼Œ
    // æ¯”å¦‚åœ¨æœåŠ¡ç«¯è¢«æ¸²æŸ“çš„æ—¶å€™ï¼Œè·å–ä¸€äº›æ•°æ®çš„æ“ä½œ
    router.onReady(() => {
      const matchedComponents = router.getMatchedComponents();
      // åŒ¹é…ä¸åˆ°çš„è·¯ç”±ï¼Œæ‰§è¡Œrejectå‡½æ•°ï¼Œå¹¶è¿”å› 404
      if (!matchedComponents.length) {
        return reject({ status: 0, data: null, msg: "404 Not Found" });
      }

      //Render the html string
      renderVueComponentToString(vm, context, (err, html) => {
        if (err) {
          console.log("Error rendering to string: ");
          console.log(err);
          return reject({
            status: 0,
            data: null,
            msg: "500 Internal Server Error"
          });
        }

        // Promiseåº”è¯¥resolveæ¸²æŸ“åçš„html
        resolve({ status: 1, data: html, msg: "200 OK" });
      });
    }, reject);
  });
};

