import Vue from "vue";
import App from "./App.vue";
import { createRouter } from "./router";

Vue.config.productionTip = false;

export function createApp() {
  // åˆ›å»º router å®ä¾‹
  const router = createRouter();
  const vm = new Vue({
    router,
    render: h => h(App)
  });

  // è¿”å› vm å’Œ router
  return { vm, router };
}
